<template>
	<div>
		<div class="collapse-button" @click="collapseevent">|||</div>
		<el-menu class="el-menu-vertical-demo" unique-opened="" router background-color="#333744" text-color="#fff" :collapse="iscollapse">
			<el-submenu index="2">
				<template slot="title">
					<i class="el-icon-s-custom"></i>
					<span>个人信息</span>
				</template>
				<el-menu-item-group>
					<el-menu-item index="/profile">
						<i class="el-icon-document"></i>
						<span slot="title">基本信息</span>
					</el-menu-item>
					
				</el-menu-item-group>
			</el-submenu>
			<el-submenu index="3">
				<template slot="title">
					<i class="el-icon-lock"></i>
					<span>账号管理</span>
				</template>
				<el-menu-item-group>
					<el-menu-item index="/revise">
						<i class="el-icon-key"></i>
						<span slot="title">修改密码</span>
					</el-menu-item>
					<el-menu-item>
						<i class="el-icon-back"></i>
						<span slot="title" @click="logout">退出</span>
					</el-menu-item>
				</el-menu-item-group>
			</el-submenu>
			<el-submenu index="4">
				<template slot="title">
					<i class="el-icon-setting"></i>
					<span>系统管理</span>
				</template>
				<el-menu-item-group>
					<el-menu-item index="/release">
						<i class="el-icon-position"></i>
						<span slot="title">发布</span>
					</el-menu-item>
					<el-menu-item index="/examine">
						<i class="el-icon-view"></i>
						<span slot="title">审核</span>
					</el-menu-item>
					<el-menu-item index="/auditLevel">
						<i class="el-icon-edit-outline"></i>
