					</el-menu-item>
				</el-menu-item-group>
			</el-submenu>
		</el-menu>
	</div>
</template>

<script>
	export default {
		name: 'asidemenu',
		data()
		{
			return{
				iscollapse:false
			}
			},
		methods: {
			logout() {
				window.sessionStorage.clear();
				this.$router.push('/login')
			},
			collapseevent()
			{
				this.iscollapse=!this.iscollapse;
				this.$emit('collapseevent',this.iscollapse)
			}
			

		}
	}
</script>

<style>
	.el-menu {
		border-right: none;
	}

	.collapse-button {
		background-color: #4a5064;
		font-size: 10px;
		line-height: 24px;
		color: #FFFFFF;
		text-align: center;
		letter-spacing: 0.2em;
		cursor: pointer;
	}
</style>
