<template>

	<el-container class="home-container">
		<el-aside :width="iscollapse?'64px':'200px'">
			<keep-alive>			
				<asidemenu @collapseevent="buttoncollapse"></asidemenu>
			</keep-alive>
		</el-aside>
		<el-main>
			<keep-alive>
				<router-view></router-view>
			</keep-alive>
		</el-main>
	</el-container>
</template>

<script>
	import asidemenu from '../components/asidemenu.vue'
	export default {
		name: 'Home',
		data()
		{
			return{
				iscollapse:false
			}
		},
		components: {
			asidemenu
		},
		methods:
		{
			buttoncollapse(data)
			{
				this.iscollapse=data
			}
		}
		
	}
</script>

<style scoped>
	.home-container {
		height: 100vh;
	}

	.el-aside {
		background-color: #333744;
		
	}

	.el-main {
		background-color: #FFFFFF;
	}
	
</style>
